.sel-mid {
  background-color: #120A8F;
  color: white;
}

.sel-start {
  width: 0.2rem;
  cursor: ew-resize;
  color: transparent;
  display: inline-block;
  overflow: visible;
  background-color: #4187ee;
  height: fit-content;
  vertical-align: top;
  position: relative;
}
.sel-start:before {
  content: "";
  display: block;
  background-color: #4187ee;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 100%;
  position: absolute;
  left: -0.15rem;
  top: -10%;
}

.sel-end {
  width: 0.2rem;
  cursor: ew-resize;
  color: transparent;
  display: inline-block;
  overflow: visible;
  background-color: #4187ee;
  height: fit-content;
  vertical-align: top;
  position: relative;
}
.sel-end:after {
  content: "";
  display: block;
  background-color: #4187ee;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 100%;
  position: absolute;
  left: -0.15rem;
  bottom: -10%;
}